  --tw-text-opacity: 1;
  color: rgb(var(--color-primary) / var(--tw-text-opacity));
}
.dark .breadcrumb {
  --tw-text-opacity: 1;
  color: rgb(var(--color-slate-300) / var(--tw-text-opacity));
}
.breadcrumb.breadcrumb-light {
  color: rgb(255 255 255 / var(--tw-text-opacity));
  --tw-text-opacity: 0.9;
}
