  color: rgb(255 255 255 / var(--tw-text-opacity));
  --tw-text-opacity: 0.8;
}
.breadcrumb .breadcrumb-item:not(:first-child) {
  position: relative;
  margin-left: 1.25rem;
  padding-left: 0.125rem;
}
